
import {mapState} from 'vuex'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name:'topiclist',
props:['name'],
components: {
Cell,Group,Scroller,Tab,TabItem,
},
computed:{
...mapState(['topics']),
},
data:function(){
return{
lists:[],
page:1,//当前页数
limit:6,//每页显示的数量
pullupconfig:{ //上拉加载更多配置文件
content:'上拉加载更多',
downContent:'松开后进行加载',
upContent:'加载更多',
loadingContent:'加载中...'
},
pulldownconfig:{ //下拉加载更多配置文件
content:'下拉刷新',
downContent:'松开后进行刷新',
upContent:'刷新更多',
loadingContent:'刷新中...'
}
}
},
methods:{
routeTo(index){//tab切换

this.$router.push('/topiclist/'+this.topics[index].name)
},
loadMoreDate(){

this.page=this.page+1;
var that=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){








that.lists.push(...res.data.data);



that.$refs.pull.donePullup();
})
},
downMore(){
this.page=1;
var that=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
that.lists=res.data.data



that.$refs.pull.donePulldown();
})
}

},



beforeRouteEnter(to,from,next){





next(function(vm){

vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){

vm.lists=res.data.data
})
})
},
beforeRouteUpdate:function(to,from,next){




var that=this
this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
console.log(res.data);
that.lists=res.data.data
})

next()
}
}



