
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
data(){
return{
title:'',
content:'',
toastText:'',
isShowToast:false,
toolbars: {
bold: true, // 粗体

header: true, // 标题








link: true, // 链接
imagelink: true, // 图片链接




htmlcode: true, // 展示html源码
help: true, // 帮助
/* 1.3.5 */


trash: true, // 清空
save: false, // 保存（触发events中的save事件）
/* 1.4.2 */

/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */


}
}
},
methods:{

uploadImage(pos,file){
var app=this;



var params = new FormData();
params.append('smfile', file);
axios.post('https://sm.ms/api/v2/upload',params,{

header:{'Content-Type': 'multipart/form-data'}
}).then(function(res){

console.log(res)




$app.$refs.md.$img2Url(pos, res.data.message.url);
})
},
addtopic(){
var app=this;
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev'  
}).then(function(res){
console.log(res);
app.$router.replace('/topiclist/dev')
}).catch(function(error){


app.isShowToast=true;
app.toastText='内容不能为空或者已经超出限制'
})
}
}
}



